.snow {

  main {
    display: block;

    position: relative;
    overflow: hidden;
    background: radial-gradient(ellipse at bottom, #1b2735 0%, #090a0f 100%);

    .snowflake {
      position: absolute;
      width: 10px;
      height: 10px;
      background-color: #fff;
      border-radius: 50%;
      box-shadow: 0 0 10px white;
      // filter: drop-shadow(0 0 10px white);
    }
  }
}
