.float-text-menu {

  main {

    ul {
      list-style: none;

      li {
        a {
          display: flex;
          padding: 6px;
          color: #fff;
          font-size: .24rem;
          text-decoration: none;
          cursor: pointer;
          -webkit-tap-highlight-color: transparent;
          overflow: hidden;

          span {
            position: relative;
            transition: .3s;

            &::before {
              content: attr(data-text);
              position: absolute;
              transform: translateY(130%);
            }
          }

          &:hover {
            span {
              transform: translateY(-130%);
            }
          }
        }
      }
    }

  }

}