.pagination {

  main {
    background-color: #eceffc;

    ul {
      display: flex;
      padding: .1rem .2rem;
      background-color: #fff;
      border-radius: .5rem;
      list-style: none;
      
      box-shadow:
      0 0.3px 0.6px rgba(0, 0, 0, 0.056),
      0 0.7px 1.3px rgba(0, 0, 0, 0.081),
      0 1.3px 2.5px rgba(0, 0, 0, 0.1),
      0 2.2px 4.5px rgba(0, 0, 0, 0.119),
      0 4.2px 8.4px rgba(0, 0, 0, 0.144),
      0 10px 20px rgba(0, 0, 0, 0.2);

      li {
        margin: 0 5px;

        button {
          width: 100%;
          height: 100%;
          outline: none;
          border: none;
          border-radius: 50%;
          font-size: .2rem;
          font-weight: bold;
          color: #777;
          cursor: pointer;
          background-color: transparent;
          transition: .3s ease;
        }

        &.page-number {
          width: .5rem;
          height: .5rem;

          &:hover button {
            color: #fff;
            background-color: #777;
          }

          &.active button {
            color: #fff;
            background-color: #333;
          }
        }
  
        &.page-prev,
        &.page-next {
          &:hover path {
            fill: darken(#777, 50%);
          }
        }
        &.page-prev {
          margin-right: .2rem;
        }
        &.page-next {
          margin-left: .2rem;
        }
      }
    }

    @media screen and (max-width: 767px) {
      transform: scaleX(0.714);
      transform-origin: left;

      ul {
        transform: scaleX(0.9) scaleY(0.64);
      }
    }
  }
}