.right-wrapper {
  flex: 1;
  position: relative;
  height: 100%;

  > section {
    display: flex;
    flex-flow: column;
    height: 100%;
    box-shadow: 0 0 5px #333;
    color: #ddd;
    background-color: #333;
    transition: height .4s ease;
    overflow: hidden;
    
    > h4 {
      box-sizing: border-box;
      padding: .16rem .38rem;
      background-color: #444;
      position: relative;
      z-index: 9;
    }

    > main {
      flex: 1;
      padding: .1rem;
    }
  }
  main {
    display: flex;
    justify-content: center;
    align-items: center;
  }

}
