.navtab {
  main {
    background: #eceffc;

    .nav-list {
      position: relative;
      display: flex;
      width: 6rem;
      height: 1.5rem;
      border: .2rem solid #fff;
      border-radius: 5% 5% 15% 15% / 15% 15% 50% 50%;
      background-color: #fff;
      overflow: hidden;

      .nav-item {
        display: flex;
        flex-direction: column;
        justify-content: center;
        width: 1.3rem;
        color: #0288d1;
        cursor: pointer;
        -webkit-tap-highlight-color: transparent;
        transition: padding .5s ease;

        span {
          position: relative;
          z-index: 1;
          transition: .5s ease;

          &.nav-icon {
            content: '';
            display: block;
            height: .3rem;
          }
          &.nav-text {
            text-align: center;
            opacity: 0;
          }
        }

        &-home .nav-icon {
          background: url('../../images/home.svg') no-repeat center / contain;
        }
        &-explore .nav-icon {
          background: url('../../images/explore.svg') no-repeat center / contain;
        }
        &-collection .nav-icon {
          background: url('../../images/collection.svg') no-repeat center / contain;
        }
        &-help .nav-icon {
          background: url('../../images/help.svg') no-repeat center / contain;
        }

        &-active {
          padding: 0 .4rem;
          
          span.nav-text {
            opacity: 1;
          }
          span.nav-icon {
            transform: translateY(-60%) scale(1.2);
          }
        }
      }

      --active-index: 0;
      --item-width: 1.3rem;
      &::before {
        content: '';
        position: absolute;
        z-index: 1;
        top: 0;
        left: 0;
        width: 2.1rem;
        height: 100%;
        border-radius: .2rem;
        background-color: #b3e5fc;
        cursor: pointer;
        transform: translateX(calc(var(--item-width) * var(--active-index)));
        transition: transform .5s ease;
      }

      @media screen and (max-width: 768px) {
        width: 3.2rem;
        height: .8rem;
        border: .1rem solid #fff;

        .nav-item {
          width: .7rem;

          span {
            &.nav-icon {
              height: .3rem;
            }
            &.nav-text {
              font-size: .12rem;
            }
          }

          &-active {
            padding: 0 .2rem;

            span.nav-icon {
              transform: translateY(-30%) scale(1.2);
            }
          }
        }
        
        --item-width: .7rem;
        &::before {
          width: 1.1rem;
        }
      }
    }
    
  }
  
}