
.menu-btn {
  position: absolute;
  top: 4px;
  right: 10px;
  width: .45rem;
  height: .45rem;
  background: url('../../images/menu.svg') no-repeat center / 50%;
  transition: .2s ease-in-out;
  overflow: hidden;
  z-index: 19;
  display: none;
}

.left-side-bar {
  display: flex;
  flex-direction: column;
  border-right: 4px solid #444;
  padding: .2rem 0;
  background-color: #444;
  overflow-y: auto;
  &::-webkit-scrollbar {
    width: 3px;
  }
  &::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background-color: rgba(255,255,255,0.2);
  }

  .nav {
    padding: 10px 20px;
    border-bottom: 2px solid #333;
    text-decoration: none;
    white-space: nowrap;
    color: #fff;
    
    &:nth-last-child(1) {
      border: none;
    }
  }
}

@media screen and (max-width: 768px) {
  .menu-btn {
    display: block;
  }
  
  .left-side-bar {
    position: absolute;
    top: 0;
    right: 0;
    max-height: 100%;
    padding: .45rem .25rem .1rem;
    box-shadow: 0 0 10px #333;
    transform: scale(0);
    transform-origin: top right;
    transition: transform .3s ease-in-out;
    z-index: 8;

    &-active {
      transform: scale(1);
    }

    &-active ~ .menu-btn {
      transform: rotate(-45deg);
    }
  }
}